export default{
	updateUserInfo(state,userInfo){
		uni.setStorageSync('userInfo',userInfo);//userInfo
		state.userInfo = userInfo;//storeuserInfo
	},
	// 
	setHistory(state,val){ 
		let list = state.historyList;
		// findIndex -1
		// list
		if(list.length>0 && list.findIndex(item =>item===val)>-1) return;
		list.unshift(val);//val;
		uni.setStorageSync('historyList',list);//list
		state.historyList = list;//statehistoryList
	},
	// 
	clearHistory(state){
		uni.removeStorageSync('historyList');
		state.historyList = [];
	},
	// 
	setLabelList(state,labelList){
		uni.setStorageSync('labelList',labelList);//list
		state.labelList = labelList;//statehistoryList
	}
}