// 

export default{
	install(Vue){//uniappVue
		Vue.mixin({
			data(){
				return{
					userRules: {
						loginName:{
							rules:[{
								required:true,
								errorMessage:''
							},{
								validateFunction:this.validateLoginName
							}]
						},
						password:{
							rules:[{
								required:true,
								errorMessage:''
							}]
						},
						phone:{
							rules:[{
								required:true,
								errorMessage:''
							},{
								validateFunction:this.validatePhone
							}]
						},
						vCode:{
							rules:[{
								required:true,
								errorMessage:''
							},{
								validateFunction:this.validateCode
							}]
						},
					},
					mobileReg:/^((13[0-9])|(14[5,7])|(15[0-3,5-9])|(17[0,3,5-8])|(18[0-9])|166|198|199|(147))\d{8}$/,
				}
			},
			methods:{
				// input
				validateLoginName(rule,val,data,callback){
					switch(true){
						case val.length<6:
						callback('');
						break;
						default ://
						return true;
					}
				},
				validatePhone(rule,val,data,callback){
					switch(true){
						case !this.mobileReg.test(val):
						callback('');
						break;
						default ://
						return true;
					}
				},
				validateCode(rule,val,data,callback){
					switch(true){
						case val != this.returnCode:
						callback('');
						break;
						default ://
						return true;
					}
					return true;
				}
			}
		})
	}
}