//Vue
//  
import {mapState,mapMutations} from 'vuex'
export default{
	install(Vue){
		Vue.mixin({
			data(){
				return {
					
				}
			},
			computed:{
				...mapState(['userInfo'])
			},
			methods:{
				...mapMutations(['updateUserInfo']),
				// 
				checkedLogin(){
					return new Promise((resolve)=>{
						// stateuserInfo resolve(),
						if(this.userInfo){
							resolve()
						}else{
							uni.navigateTo({
							url:'/pages/userInfo/login/login'
							})
						}
					})
				}
			}
		})
	}
}