export default({name,data={}})=>{
	// promise
	return new Promise((resolve,reject)=>{
		const loading = data.isLoading ;
		loading && delete data.isLoading;//isLoadinghttp.js
		// loading
		!loading && uni.showLoading({
			title:""
		});
		// 
		uniCloud.callFunction({
			name,//
			data,
			success({result}){
				// 
				if(result.code === 0){
					resolve(result.data);
				}else{
					//  
					uni.showToast({
						icon:"none",
						title:result.msg
					});
				}
			},
			// 
			fail(err){
				reject(err);
			},
			complete(){
				//  loading
				!loading && uni.hideLoading();
			}
		})
	})
}